import React, { useState } from "react";
import { getBar } from "../services/fakeBar";
import BarCard from "./common/BarCard";
import Accordion from "react-bootstrap/Accordion";
import BarCardBody from "./common/BarCardBody";

function Bar(props) {
	const [bar, setBar] = useState(getBar());

	return (
		<Accordion>
			<div className="row justify-content-center">
				{bar.map((item) => (
					<div
						style={{
							marginRight: 5,
							marginLeft: 5,
							justifyContent: "center",
						}}
					>
						<div>
							<Accordion.Toggle
								style={{
									borderWidth: 0,
									marginTop: 20,
									padding: 0,
									width: 240,
								}}
								eventKey={item._id}
							>
								<BarCard key={item._id} item={item} />
							</Accordion.Toggle>
						</div>
						<Accordion.Collapse
							eventKey={item._id}
							style={{ width: 240 }}
						>
							<div>
								<BarCardBody
									info={item.info}
									ingridients={item.ingridients}
								/>
							</div>
						</Accordion.Collapse>
					</div>
				))}
			</div>
		</Accordion>
	);
}

export default Bar;
