import React from "react";

let styles = {
	card: {
		backgroundColor: "#445EEA",
		borderRadius: "8px",
		overflow: "hidden",
		border: "1px solid #445EEA",
		width: "100%",
	},
	text: {
		textAlign: "center",
		padding: "10px",
		color: "white",
	},
};

function BarCard({ item }) {
	return (
		<div style={styles.card}>
			<a>
				<img
					src={`${item.image}`}
					alt={item._id}
					height={240}
					width={240}
				/>
			</a>
			<div style={styles.text}>{item.title}</div>
		</div>
	);
}

export default BarCard;
