import React from "react";

let styles = {
	card: {
		margin: "20px",
		backgroundColor: "#445EEA",
		borderRadius: "8px",
		overflow: "hidden",
		border: "1px solid #445EEA",
	},
	text: {
		textAlign: "center",
		padding: "10px",
		color: "white",
	},
};

function RecipeCard({ item }) {
	const onCardClick = (file) => {
		window.open(file);
	};

	return (
		<div style={styles.card}>
			<div
				onClick={() => onCardClick(item.recipeUrl)}
				target="_blank"
				rel="noreferrer"
			>
				<img
					src={`${item.image}`}
					alt={item._id}
					width={240}
					height={240}
				/>
			</div>
			<div style={styles.text}>{item.title}</div>
		</div>
	);
}

export default RecipeCard;
