import React from "react";

let styles = {
	card: {
		backgroundColor: "#DFEDF7",
		borderRadius: 8,
		overflow: "hidden",
		border: "1px solid #445EEA",
	},
	text: {
		color: "black",
		fontSize: 12,
		paddingLeft: 10,
		paddingRight: 10,
	},
	header: {
		color: "black",
		padding: 5,
	},
	ingridients: {
		display: "flex",
		justifyContent: "space-between",
		paddingRight: 5,
	},
};

function BarCardBody({ info, ingridients }) {
	return (
		<div style={styles.card}>
			<div style={styles.header}>Ингридиенты</div>
			{ingridients.map((item) => (
				<div style={styles.ingridients}>
					<div style={styles.text}>{item.name}</div>
					<div style={styles.text}>{item.quantity}</div>
				</div>
			))}
			<div style={styles.header}>Способ Приготовления</div>
			{info.map((item, index) => (
				<div style={styles.text}>{index + 1 + ") " + item}</div>
			))}
		</div>
	);
}

export default BarCardBody;
